fill: rgb(0,0,0,0);\n  stroke: var(--dark);\n  overflow: visible; /** Prevents icon from getting cropped **/\n}\n\n.tool-icon:hover {\n  cursor: pointer;\n  transform: scale(1.1);\n}\n\n#moon-dark {\n  fill: var(--dark);\n  stroke: var(--dark);\n}\n#moon-shadow {\n  fill: var(--light);\n  stroke: var(--light);\n}\n\n.menu-icon-close #menu-rect-horizontal {\n  animation-name: x-to-horizontal;\n  animation-duration: 1s;\n  transform: rotate(-90deg);\n  transform-origin: center;\n  transition: transform 1s;\n}\n\n.menu-icon-close #menu-rect-vertical {\n  animation-name: x-to-vertical;\n  animation-duration: 1s;\n  transform: rotate(-90deg);\n  transform-origin: center;\n  transition: transform 1s;\n}\n\n/** Opening animation **/\n.menu-icon-open #menu-rect-horizontal {\n  animation-name: horizontal-to-x;\n  animation-duration: 1s;\n  transform: rotate(45deg);\n  transform-origin: center;\n  transition: transform 1s;\n}\n\n.menu-icon-open #menu-rect-vertical {\n  animation-name: vertical-to-x;\n  animation-duration: 1s;\n  transform: rotate(45deg);\n  transform-origin: center;\n  transition: transform 1s;\n}\n\n@keyframes horizontal-to-x {\n  0% {\n    width: 100%;\n    x: 0;\n  }\n  50% {\n    width: 0%;\n    x: 50%;\n  }\n  100% {\n    width: 100%;\n    x: 0;\n  }\n}\n\n@keyframes vertical-to-x {\n  0% {\n    height: 100%;\n    y: 0;\n  }\n  50% {\n    height: 0%;\n    y: 50%;\n  }\n  100% {\n    height: 100%;\n    y: 0;\n  }\n}\n/** Same as above keyframes **/\n@keyframes x-to-horizontal {\n  0% {\n    width: 100%;\n    x: 0;\n  }\n  50% {\n    width: 0%;\n    x: 50%;\n  }\n  100% {\n    width: 100%;\n    x: 0;\n  }\n}\n\n@keyframes x-to-vertical {\n  0% {\n    height: 100%;\n    y: 0;\n  }\n  50% {\n    height: 0%;\n    y: 50%;\n  }\n  100% {\n    height: 100%;\n    y: 0;\n  }\n}\n\n/** Lists **/\n.list {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  font-size: 36px;\n  margin: auto;\n  -webkit-text-fill-color: var(--dark);\n  -webkit-text-stroke-width: 1px;\n  -webkit-text-stroke-color: var(--dark);\n}\n\n.dark-mode .list {\n  -webkit-text-stroke-color: var(--light);\n  -webkit-text-fill-color: var(--dark);\n}\n\n.list > * {\n  transition: transform .4s;\n  transform-origin: left center;\n  padding: 0.5em;\n  border-bottom: 1px solid var(--dark);\n}\n\n.dark-mode .list > * {\n  border-color: var(--light);\n}\n\n.list > *:hover:not(.projects-header) {\n  transform: translateX(10px);\n}\n\n.projects-list {\n  margin: 0;\n  max-width: 500px;\n  flex-grow: 1;\n}\n\n/** Menu **/\n.menu {\n  font-size: 36px;\n  max-width: 700px;\n  margin: auto;\n}\n\n.menu-arrow {\n  display: none;\n  padding-right: 1em;\n  width: 40px;\n  height: 16px;\n  vertical-align: middle;\n}\n\n/** Projects Page **/\n.projects-container {\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n}\n\n.projects-header {\n  font-size: 36px;\n}\n\n.projects-left {\n  width: 100%;\n  height: 500px;\n  margin-top: 2em;\n  background-position: top center;\n  background-size: contain;\n  background-repeat: no-repeat;\n  transform: translateX(-10px);\n  transition: opacity 0.5s ease-in-out;\n  flex-grow: 1;\n  opacity: 0;\n}\n\n.opacity {\n  opacity: 1;\n}\n\n@media (max-width: 800px) {\n  .projects-container {\n    flex-direction: column;\n  }\n  .projects-img {\n    display: none;\n  }\n  .projects-list {\n    margin: 0;\n    padding: 0;\n  }\n}\n\n/** Project Page **/\n.project-page {\n  height: 100%;\n}\n\n.project-info {\n  display: flex;\n  flex-direction: row;\n}\n\n.project-details {\n  display: flex;\n  flex-direction: row;\n  max-width: 500px;\n  width: 100%;\n}\n\n\n.project-details > * {\n  width: 100%;\n}\n\n.project-main {\n  display: flex;\n  flex-direction: row;\n}\n\n.project-desc {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  width: 100%;\n}\n\n.project-header {\n  display: flex;\n  flex-direction: row;\n}\n\n.project-header h1 {\n  flex-grow: 3;\n}\n\n.photos {\n  max-width: 1000px;\n  margin: auto;\n  margin-top: 10px;\n}\n\n.photos > * {\n  max-width: 1000px;\n  margin: auto;\n}\n\n.next-project-btn {\n  text-align: right;\n  border-top: 1px solid var(--dark);\n  border-bottom: 1px solid rgb(0,0,0,0);\n  width: 100%;\n  margin-top: 10px;\n}\n\n.dark-mode .next-project-btn {\n  border-top-color: var(--light);\n}\n\n.next-project-btn * {\n  display: inline-block;\n}\n\n.next-project-btn:hover, .see-projects-btn:hover {\n  border-color: var(--dark);\n}\n\n.dark-mode .next-project-btn:hover, .dark-mode .see-projects-btn:hover {\n  border-color: var(--light);\n}\n\n#project-next-arrow {\n  width: 20px;\n  height: 8px;\n  padding-left: 10px;\n  vertical-align: middle;\n  transition: transform 0.2s;\n  transform-origin: left center;\n  stroke: var(--dark);\n}\n\n/* .next-project-btn:hover #project-next-arrow {\n  transition: transform 0.2s;\n  transform: scale(1.5);\n  transform-origin: left center;\n} */\n\n.see-projects-btn {\n  border-bottom: 1px solid rgb(0,0,0,0);\n  margin-top: 1em;\n  padding-bottom: 1em;\n  width: 100%;\n  display: inline-block;\n}\n\n.see-projects-btn * {\n}\n\n@media (max-width: 800px) {\n  .project-info, .project-details, .project-header {\n    flex-direction: column;\n  }\n  .next-project-btn {\n    width: 100%;\n    margin-top: 10px;\n  }\n}\n\n/** Repo Cards **/\n.repos {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n}\n\n.repoCard {\n  border: 1px solid rgb(0,0,0,0.5);\n  border-radius: 5px;\n  padding: 10px;\n  margin: 10px;\n  flex-grow: 1;\n}\n\n.languageBlob {\n  width: 12px;\n  height: 12px;\n}\n\n.colorJavaScript {\n  stroke: yellow;\n  fill: yellow;\n}\n\n.colorHTML {\n  stroke: red;\n  fill: red;\n}\n\n.colorCSS {\n  stroke: pink;\n  fill: pink;\n}\n\n\n/** Dark Mode **/\n\n.dark-mode, .dark-mode * {\n  color: var(--light);\n  background-color: rgb(50,50,50);\n}\n\n.dark-mode svg:not(.languageBlob) {\n  stroke: var(--light) !important;\n}\n"],"sourceRoot":""}]);
